* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  font-family: inherit;
}
html {
  box-sizing: border-box;
}
body {
  font-family: "Ubuntu", sans-serif;
  display: flex;
  justify-content: center;
  background-color: lightgray;
}
hr {
  border: none;
  border-top: 2px solid #000000;
  margin: 20px 2px;
}

#divform {
  margin: 15px 0;
}

.inputtextoform,
.cantidad-form {
  width: 100%;
  height: 35px;
  border-radius: 5px;
  border: none;
  background: rgb(177, 255, 253) !important;
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.75);
  border-bottom: 2.5px solid rgb(168, 231, 233);
  border-right: 2.5px solid rgb(168, 231, 233);
  text-align: center;
}
.cantidad-form,
.inputtextoform::placeholder {
  text-align: center;
  font-size: 20px;
  color: #3d3d3d;
}
.boton-agregar {
  background: rgb(235, 146, 190);
  color: #ffffff;
  width: 100px;
  height: 40px;
  font-size: 20px;
  border-radius: 5px;
  border: none;
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.75);
  border-bottom: 2.5px solid rgb(238 117 177);
  border-right: 2.5px solid rgb(238 117 177);
}
.maindiv {
  width: 500px;
  height: auto;
  margin: 60px;
  padding: 30px;
  display: flex;
  flex-direction: column;
}

#formtodolist {
  background: rgb(255, 194, 136);
  padding: 20px;
  text-align: center;
}

.divtabla {
  background: rgb(254, 168, 47);
  padding: 20px;
}

.container-li {
  border-radius: 5px;
  border: none;
  background: rgb(177, 255, 253);
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.75);
  border-bottom: 2.5px solid rgb(168, 231, 233);
  border-right: 2.5px solid rgb(168, 231, 233);
  margin: 10px;
  padding: 4px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.botonborrar {
  padding: 2px;
  margin-left: 20px;
  border-radius: 5px;
  border: none;
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.75);
  border-bottom: 2.5px solid rgb(238 117 177);
  border-right: 2.5px solid rgb(238 117 177);
  background: rgb(235, 146, 190);
  color: #ffffff;
}

.texto {
  flex-grow: 1;
}
